.alignwide .wp-block-wptravel-trips-list{
	max-width: initial !important;
}

.wptravel-archive-wrapper.grid-view {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 2em;
	.view-box {
		display: block;
		margin-bottom: 0;
		padding-left: 0;
		background: #fff;
		border-radius: 4px;
		box-shadow: 0 4px 8px rgba(0,0,0,.04);
		transition: all ease .3s;
		.view-image {
			width: 100%;
			margin: 0;
			height: 230px;
			position: relative;
			img {
				width: 100%;
				border-top-left-radius: 4px;
				border-top-right-radius: 4px;
			}
			.offer {
				padding: 5px 12px;
				left: 15px;
				bottom: -15px;
				position: absolute;
				display: flex;
				align-items: center;
				background: #f03208;
				span {
					font-size: 14px;
					font-weight: 500;
					text-transform: uppercase;
					color: #fff;
				}
			}
		}
		.view-content {
			display: flex;
    		flex-wrap: nowrap;
			flex-direction: column;
			margin: 10px 0;
			padding: 15px 15px 0;
			width: auto;
			.left-content {
				width: 100%;
				padding-right: 0;
				border-bottom: none;
				padding-bottom: 0;
				header {
					display: flex;
					justify-content: space-between;
					flex-wrap: nowrap;
					align-items: baseline;
					gap: 5px;
					.entry-title {
						width: 90%;
						margin: 5px 0;
						text-align: left;
						.heading-link {
							width: 100%;
							text-align: center;
							font-size: 24px;
							line-height: 30px;
							display: inline;
							text-transform: capitalize;
						}
					}
					.favourite {
						width: 10%;
						text-align: right;
						.wp-travel-add-to-wishlists{
							color: #f83531;
							font-size: 0;
						}
						i {
							font-size: 18px;
						}
					}
				}
				.trip-icons {
					display: grid;
					grid-template-columns: repeat(2, 1fr);
					-moz-column-gap: 3em;
					column-gap: 3em;
					position: relative;
					border: 1px solid #e9e9e9;
					border-left: 0;
					border-right: 0;
					padding: 20px 10px;
					margin: 15px 0;
					gap: 0.7em;
					justify-content: space-between;
					i {
						font-size: 16px;
					}
					span {
						font-size: 13px;
					}
				}
				
			}
			.right-content {
				width: 100%;
				text-align: left;
				margin: 20px 0;
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;
				flex-direction: column;
				border-left: none;
				.footer-wrapper {
					margin-bottom: 1em;
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: space-between;
					width: 100%;
					flex-direction: row-reverse;
					.trip-price {
						margin-bottom: 0;
						text-align: right;
						.price-here {
							display: block;
							font-size: 26px;
							font-weight: 600;
							color: #484848;
							margin: 10px 0 -10px;
						}
					}
					.wp-travel-average-review::before {
						color: rgb(249, 160, 50);
						content: "";
						float: left;
						left: 0px;
						position: absolute;
						top: 0px;
					}
					.wp-travel-average-review {
						display: block;
						margin: auto;
						font-family: "Font Awesome 5 Free";
						font-size: 1em;
						height: 1em;
						line-height: 1;
						overflow: hidden;
						position: relative;
						width: 5.5em;
						word-break: normal;
						span::before {
							content: "";
							left: 0px;
							position: absolute;
							top: 0px;
							font-family: "Font Awesome 5 Free";
						}
						span {
							float: left;
							left: 0;
							overflow: hidden;
							padding-top: 1.5em;
							position: absolute;
							top: 0;
							color: #f9a032;
							font-weight: 900;
						}
					}
					.wp-travel-review-text {
						margin-left: 0;
						font-size: 12px;
    					line-height: 1;
					}
					
				}
				.explore-btn {
					width: 100%;
					text-align: center;
					border-radius: 3px;
					padding: 12px 15px;
					transition: all ease .3s;
				}
			}
		}
	}
}

.view-image img{
	width: 100% !important;
}

